body {
  background-image: url(../../../build/img/pattern-background-desktop.svg);
  background-repeat: no-repeat;
  background-position: center center;
}

.card {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 450px;
  height: 700px;
  border-radius: 1rem;
  border-style: solid;
  margin: auto;
}
.card .hero {
  background-image: url(../../../build/img/illustration-hero.svg);
  background-repeat: no-repeat;
  background-size: none;
  height: 220px;
}